body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f8f9fa; /* Light Bootstrap grey */
  color: #212529; /* Bootstrap text color */
}

.card {
  background: white;
  border-radius: 0.5rem;
  box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075);
  max-width: 600px;
  margin: 2rem auto;
  padding: 1.5rem;
  text-align: center;
}

.card-title {
  font-size: 1.75rem;
  font-weight: bold;
  color: #007bff; /* Bootstrap primary */
}

.card-description {
  font-size: 1rem;
  color: #6c757d; /* Bootstrap secondary text */
}

.lives {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
  margin: 1rem 0;
}

.life-box {
  width: 1.5rem;
  height: 1.5rem;
  background-color: #6c757d; /* Bootstrap grey */
  border-radius: 50%;
}

.life-box.active {
  background-color: #28a745; /* Bootstrap success */
}

.life-box.inactive {
  background-color: #e0e0e0; /* Grey for lost lives */
}

.word-grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 0.75rem;
  margin: 1.5rem 0;
}

.word-box {
  padding: 0.75rem;
  text-align: center;
  border: 1px solid #dee2e6; /* Bootstrap light border */
  border-radius: 0.25rem;
  background-color: #f8f9fa; /* Light grey */
  font-weight: bold;
  font-size: 1rem;
}

.word-box.revealed {
  background-color: #c3e6cb; /* Bootstrap success background */
}

.input-group {
  display: flex;
  gap: 0.5rem;
}

input[type="text"] {
  flex: 1;
  padding: 0.75rem;
  border: 1px solid #ced4da; /* Bootstrap input border */
  border-radius: 0.25rem;
  font-size: 1rem;
}

button {
  padding: 0.75rem 1rem;
  border: none;
  border-radius: 0.25rem;
  font-size: 1rem;
  cursor: pointer;
  background-color: #007bff; /* Bootstrap primary */
  color: white;
  transition: background-color 0.15s ease-in-out;
}

button:hover {
  background-color: #0056b3; /* Bootstrap primary hover */
}

button:disabled {
  background-color: #6c757d; /* Bootstrap grey */
  cursor: not-allowed;
}

.alert {
  padding: 1rem;
  border-radius: 0.25rem;
  margin: 1rem 0;
  font-size: 1rem;
}

.alert.success {
  background-color: #d4edda; /* Bootstrap success background */
  color: #155724; /* Bootstrap success text */
}

.alert.error {
  background-color: #f8d7da; /* Bootstrap danger background */
  color: #721c24; /* Bootstrap danger text */
}

.next-button {
  margin-top: 1.5rem;
  background-color: #28a745; /* Bootstrap success */
  color: white;
}

.next-button:hover {
  background-color: #218838; /* Bootstrap success hover */
}